<template>
	<div class="right">
		<div class="head">
			<span>轮播图管理</span>
			<Button type="primary" class="add" @click="showAdd">新增</Button>
		</div>
		<Table :columns="columns1" :data="carousels"></Table>
		
		<!--删除提示对话框-->
		<Modal v-model="modal1" width="360">
	        <p slot="header" style="color:#f60;text-align:center">
	            <Icon type="information-circled"></Icon>
	            <span>删除提示</span>
	        </p>
	        <div style="text-align:center">
	            <p>你确定要删除这个轮播图么？</p>
	        </div>
	        <div slot="footer">
	        	<Button  @click="closeDel">取消</Button>
	            <Button type="error" @click="delBanner">删除</Button>
	        </div>
	    </Modal>
	    
	    <!--新增对话框-->
	    <Modal
	        v-model="modal2"
	        title="新增轮播图">
	        <Form ref="addBanner" :model="addBanner"  :label-width="80">
		        <FormItem label="标题" prop="name">
		            <Input v-model="addBanner.title" placeholder="请输入轮播图标题"></Input>
		        </FormItem>
		        <FormItem label="上传图片" prop="url">
		            <Upload
				        :on-success="handleSuccess"
				        :format="['jpg','jpeg','png']"
				        :max-size="2048"
				        :on-format-error="handleFormatError"
				        :on-exceeded-size="handleMaxSize"
				        multiple
				        type="drag"
				        action="http://localhost:2255/upload"
				        style="display: inline-block;width:58px;">
				        <div style="width: 58px;height:58px;line-height: 58px;">
				            <Icon type="camera" size="20"></Icon>
				        </div>
				    </Upload>
		        </FormItem>
		        <FormItem label="链接" prop="mail">
		            <Input v-model="addBanner.link" placeholder="请输入链接"></Input>
		        </FormItem>
		    </Form>
	    </Modal>
	</div>
</template>

<script>
	import $ from 'jquery'
	export default {
		components:{
			
		},
		data(){
			return {
				head:{
					type:'post'
				},
				//新增轮播图信息
				addBanner:{
					title:'',
					link:'',
					url:'',
					sort:'',
				},
				//新增轮播图对话框
				modal2:false,
				//删除bannerid
				delBannerId:'',
				//删除提示对话框
				modal1:false,
				columns1: [
                    {
                        title: '标题',
                        key: 'title'
                    },
                    {
                        title: '图片',
                        key: 'img',
                        align: 'center',
                        render:(h,params) => {
                        	return h('img',{
                        		attrs:{
                        			src:'http://localhost:2255/img/chc.jpg'
                        		},
                        		style:{
                        			margin:'10px',
                        			width:'50px',
                        			height:'50px'
                        		}
                        	})
                        }
                    },
                    {
                        title: '链接地址',
                        key: 'link'
                    },
                    {
                        title: '操作',
                        key: 'operate',
                        render: (h, params) => {
                        	var status = params.row.status == 1 ? '停用':'启用'
                        	var color = params.row.status == 1 ? 'error':'primary'
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: color,
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.changeStatue({
                                            	id:params.row.id,
                                            	status:params.row.status === 1? 0:1
                                            })
                                        }
                                    }
                                }, status),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                        	this.delBannerId = params.row.id
                                            this.showDel()
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ],
                //轮播图列表
	            carousels:[],
			}
		},
		methods:{
			//显示新增轮播图对话框
			showAdd(){
				this.modal2 = true
			},
			//删除轮播图
			delBanner(){
				var that = this
				$.ajax({
					type:"post",
					data:{
						id:that.delBannerId
					},
					url:"http://localhost:2255/admin/carousels/del",
					success:function(data){
						that.$Message.success('删除轮播图成功')
						that.getCarousels()
						that.closeDel()
					}
				});
			},
			//关闭删除对话框
			closeDel(){
				this.modal1 = false
			},
			//显示删除对话框
			showDel(){
				this.modal1 = true
			},
			//获取轮播图列表
			getCarousels(){
				var that = this
				$.ajax({
					type:"post",
					url:"http://localhost:2255/admin/carousels",
					success:function(data){
						if(data !== 'err'){
							that.carousels = data.data
						}
					}
				});
			},
			//更改轮播图状态
			changeStatue(obj){
				var that = this
				$.ajax({
					type:"post",
					data:obj,
					url:"http://localhost:2255/admin/carousels/status",
					success:function(data){
						that.$Message.success('更新状态成功')
						that.getCarousels()
					}
				});
			},
			handleSuccess (res, file) {
                file.url = 'https://o5wwk8baw.qnssl.com/7eb99afb9d5f317c912f08b5212fd69a/avatar';
                file.name = '7eb99afb9d5f317c912f08b5212fd69a';
            },
            handleFormatError (file) {
                this.$Notice.warning({
                    title: 'The file format is incorrect',
                    desc: 'File format of ' + file.name + ' is incorrect, please select jpg or png.'
                });
            },
            handleMaxSize (file) {
                this.$Notice.warning({
                    title: 'Exceeding file size limit',
                    desc: 'File  ' + file.name + ' is too large, no more than 2M.'
                });
            },
		},
		mounted(){
			this.getCarousels()
		}
	}
</script>

<style scoped>
	.head{
		height: 30px;
		line-height: 30px;
		margin-bottom: 20px;
	}
	.right{
		margin-left:240px;
		padding:20px;
	}
	span{
		color:#5cadff;
		font-size: 18px;
	}
	.add{
		float: right;
	}
</style>


